}
  
.nav-stripe { 
  justify-content: center;
}
   
.subheading {
 margin-top: 70px;
}

.boxy-flex-line {
  max-width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-top: 100px;
