}
}

@media only screen and (min-width: 1280px) {

header {
  width: 40%;
}
  
.content {
  display: flex;
}

.obdelniky-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 90%;
}

.obdelniky,.obdelniky-dolni{
  width: 22%; 
  margin: 10% auto;
  padding-bottom: 2px;
}

.obdelniky:hover,.obdelniky-dolni:hover {
  border-color: white;
  cursor: pointer;
}
